@import url(https://fonts.googleapis.com/css2?family=Merriweather:wght@300;400;700&family=Roboto:wght@300;400;500&display=swap);
*{padding:0;margin:0;font-family:'Roboto', sans-serif}html{color:#333;background:#cdc url("https://i.imgur.com/LtdNdBp.jpg") repeat top left}.App{font-family:sans-serif;text-align:center;margin:6rem 0}h1{font-size:4em;font-family:'Merriweather', serif}.app_description{line-height:34px;font-size:1.4em}.paragraph_holder{display:flex;justify-content:space-evenly;align-items:center;padding:2%;margin:5% auto}.recipe_img{width:35%}

.searchForm{display:grid;grid-template-columns:auto 1fr auto;grid-gap:1rem;align-items:center;margin-top:7%;padding:2%}.label{font-size:1.2rem;margin-bottom:0.2rem;text-transform:uppercase}.input{font-size:1.6rem;padding:0.5rem 2rem;line-height:2.8rem;border-radius:20px;border:1px solid #ddd;margin-bottom:1rem}.search_button{background-color:rgba(0,0,0,0.75);color:white;padding:1rem 2rem;border:1px solid rgba(0,0,0,0.75);border-radius:20px;font-size:1.4rem;cursor:pointer;transition:background-color 250ms}.recipe_list{margin-top:4rem;display:grid;grid-template-columns:repeat(auto-fit, minmax(280px, 4fr));grid-template-rows:auto;grid-gap:3%;padding:3%;-webkit-animation:fade-in 0.5s ease-out both 0.3s;animation:fade-in 0.5s ease-out both 0.3s}.recipe{padding:2rem 4rem;border-radius:10px;box-shadow:3px 7px 12px 2px #5d5d5d;margin-bottom:2rem;background-color:#a9a9a95c}.recipe_title{margin-bottom:0.5rem;font-size:1.4rem}.recipe_image{margin:0 auto;display:block}.recipe_favorite{font-size:1.2em;margin-top:5%;padding:2%}.recipe_info{font-size:1.2em}

